<script>

import { mapState, mapActions , mapGetters} from "vuex";

export default {
  name: 'Home',
  data(){
    return {
      tamPantalla: 0,
      mostrar: false,
      link: '',
      contador:0,
      comprar: [],
      nroItem: 1,
      loading: false,
      total: 0 //,
      // productos: [{
      //   numero: 'folio2',
      //   nombre: 'Vaca y Pollito',
      //   url: 'img/portfolio/circus1.png',
      //   precio: 150
      // },{
      //   numero: 'folio3',
      //   nombre: 'Soy la Comadreja',
      //   url: 'img/portfolio/cake1.png',
      //   precio: 110
      // },{
      //   numero: 'folio4',
      //   nombre: 'Rafa - Simpsons',
      //   url: 'img/portfolio/game.png',
      //   precio: 100
      // }]
    }
  },
  methods: {
    ...mapActions(['getProductos','addProductos','delProductos','putProductos',]),
    agregar(item){
      let i = {}
      i.nombre = item.nombre
      i.numero = item.numero
      i.precio = parseFloat(item.precio)
      i.id = this.nroItem
      i.uid = item.uid

      this.comprar.push(i);
      this.contador = this.comprar.length
      this.nroItem++
      this.total+= i.precio
      this.link=''
      this.loading = false
    },
    eliminar(id){
      this.comprar = this.comprar.filter(doc => {
          return doc.id != id
        })
      this.total = 0
      this.comprar.forEach((p) =>{
        this.total+= p.precio
      });
      this.contador = this.comprar.length
      this.link=''
      this.loading = false
    },
    ver(){
      var tam = $(window).width()/2
      if(this.contador > 0 && (tam <= 496)){
        this.mostrar = true
      }else{
        this.mostrar = false
      }
    },
    clickMP(){
      // debugger
      this.loading = true
      // debugger
      this.axios.post('/mp',this.comprar)
      .then(res => {
        console.log(res);
        // debugger
        this.link = res.data.init_point;
        console.log(this.link);
        this.loading = false
      })
      .catch(error => {
        console.log(error.response);
        this.loading = false
      })
    }
  },
  computed: {
    ...mapState(['productos'])
  },
  created() {
    this.tamPantalla = $(window).width()/2 -100 ;
  },
  mounted() {
    this.getProductos();
    $(window).resize(function(){
      //aqui el codigo que se ejecutara cuando se redimencione la ventana
      this.tamPantalla = $(window).width()/2 -100
      $("#finalizar").css("right", this.tamPantalla +"px");
      $("#tam").click(); 
    })
  },
}
</script>

